<template>
  <div class="note-forward">
    <div class="forward-list">
      <div class="left">
        <div class="top-search">
          <i class="iconfont icon-sousuo"></i>
          <input
            type="text"
            :placeholder="$t('favorites.searchText')"
            v-model="keyWords"
            @input="searchContact(keyWords)"
          >
        </div>
        <div class="left-list">
          <contact-list
            :contactList="contactList"
            :selectStatus="true"
            @select-item="selectItem"
            v-if="keyWords === ''"
          ></contact-list>
          <div v-else>
            <div class="contact-ype-title" v-if="Object.keys(searchUsers).length > 0">联系人</div>
            <contact-list
              :contactList="searchUsers"
              :selectStatus="true"
              @select-item="selectItem"
            ></contact-list>
            <div class="contact-ype-title" v-if="Object.keys(searchGroup).length > 0">群聊</div>
            <contact-list
              :contactList="searchGroup"
              :selectStatus="true"
              @select-item="selectItem"
            ></contact-list>
          </div>
        </div>
      </div>
      <div class="right">
        <div class="right-top">
          <span>{{rightTip}}</span>
          <i class="iconfont icon-guanbi" @click="$emit('close')"></i>
        </div>
        <div class="selected-list">
          <contact-list
            :contactList="selectedContactList"
            :isRemoveBtn="true"
            @remove-item="removeItem"
          ></contact-list>
        </div>
        <div class="right-footer">
          <button
            @click="confirm"
            :disabled="isForward"
            :class="isForward?'forward-ban':'forward-ok'"
          >{{$t('favorites.confirm')}}</button>
          <button @click="$emit('close')">{{$t('favorites.cancel')}}</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import noteMixin from './../common/noteMixin.js';
import ContactList from './contactList.vue';
import { mapGetters } from 'vuex';
const contactData = require('./../../../static/contactData.json');
const groupData = require('./../../../static/groupData.json');
const chatList = require('./../../../static/chatList.json');
const sousuoData = require('./../../../static/sousuo.json');
export default {
  mixins: [noteMixin],
  components: {
    ContactList
  },
  data() {
    return {
      keyWords: '',
      contactList: {},
      selectedContactList: {},
      searchContant: {},
      commonParam: {
        targetId: '', // 传入会话方ID
        fromId: '', // 我的ID
        time: Date.parse(new Date()), // 发送时间
        msgType: 2 // 消息类型
      }
    }
  },
  props: {
    forwardData: Object
  },
  watch: {
    // selectedContactList: {
    //   handler: function() {
    //     ...
    //   },
    //   deep: true
    // }
  },
  computed: {
    ...mapGetters(['user_account']),
    rightTip() {
      let selectNumber = Object.keys(this.selectedContactList).length
      return selectNumber > 0 ? this.$t('favorites.selectedFrineds', { value: selectNumber }) : this.$t('favorites.selectFriends')
    },
    isForward() {
      return this.rightTip === this.$t('favorites.selectFriends');
    },
    searchUsers() {
      let temp = {};
      for (let u in this.searchContant) {
        if (this.searchContant[u].id) {
          this.$set(temp, u, this.searchContant[u])
        }
      };
      return temp
    },
    searchGroup() {
      let temp = {};
      for (let u in this.searchContant) {
        if (this.searchContant[u].groupId) {
          this.$set(temp, u, this.searchContant[u])
        }
      };
      return temp
    }
  },
  mounted() {
    this.commonParam.msgType = this.forwardData.type;
    this.commonParam.fromId = this.user_account.id;
    this.getChatList()
    // console.log(chatList, '最近联系人');
    // chatList.chats.forEach((element, index) => {
    //   if (element.chatType === 1 || element.chatType === 2) {
    //     this.$set(element, 'isSelected', false);
    //     this.$set(this.contactList, element.id, element);
    //   }
    // });
    // console.log(groupData);
  },
  methods: {
    searchContact(k) {
      console.log(k);
      this.$Service.search.searchFromLocal(
        { key: k },
        resp => {
          console.log(resp, '搜索联系人');
          if (resp.code === -16777212) {
            let allData = [];
            resp.users.forEach(element => {
              if (element.isUser) allData.push(element);
            });
            allData = allData.concat(resp.groups)
            this.searchContant = {};
            allData.forEach((element, index) => {
              let hasUser = Object.keys(this.selectedContactList)
              if (hasUser.indexOf(element.id) === -1) {
                this.$set(element, 'isSelected', false);
              } else {
                this.$set(element, 'isSelected', true);
              }
              this.$set(this.searchContant, element.id || element.groupId, element);
              console.log(this.searchContant, '搜索处理后的结果');
            });
          }
        }
      )
    },
    getChatList() {
      this.$Service.chat.getChatList(
        null,
        resp => {
          console.log(resp, 'getChatList 收藏');
          if (resp.chats && resp.chats.length > 0) {
            resp.chats.forEach((element, index) => {
              if ((element.chatType === 1 || element.chatType === 2) && element.name !== '') {
                this.$set(element, 'isSelected', false);
                this.$set(this.contactList, element.id, element);
              }
            });
          }
        }
      )
    },
    setIsSelected(index, flag) {
      console.log(index, flag, 'setIsSelected');
      // this.$set(this.contactList[index], 'isSelected', flag);
      // this.$set(this.searchContant[index], 'isSelected', flag);
      if (this.contactList.hasOwnProperty(index)) {
        this.$set(this.contactList[index], 'isSelected', flag);
      };
      if (this.searchContant.hasOwnProperty(index)) {
        this.$set(this.searchContant[index], 'isSelected', flag);
      };
    },
    selectItem(item, index) {
      if (item.isSelected === true) {
        this.setIsSelected(index, false);
        this.$delete(this.selectedContactList, index);
      } else {
        if (Object.keys(this.selectedContactList).length > 8) {
          this.$toast.tip({message: this.$t('favorites.overFriends')});
          return;
        };
        this.setIsSelected(index, true);
        this.$set(this.selectedContactList, index, item);
      }
    },
    removeItem(index) {
      this.setIsSelected(index, false);
      this.$delete(this.selectedContactList, index);
    },
    confirm() {
      let toTargetIdArr = Object.keys(this.selectedContactList);
      let isTxtType = this.commonParam.msgType === 2;
      toTargetIdArr.forEach(e => {
        this.commonParam.targetId = e;
        this.sendNote()
      });
      this.$emit('close');
      this.$toast.tip({message: this.$t('favorites.shared')});
    },
    sendNote() {
      let postMsgToRender = window.require('electron').remote.getGlobal('postMsgToRender');
      console.log('forwardData:::::', this.forwardData)
      let msgbase = {
        body: this.forwardData.content.body, // 消息体
        url: this.forwardData.content.mediaUrl || this.forwardData.content.url,
        meidaUrl: this.forwardData.content.mediaUrl, // 语音消息url
        fileName: this.forwardData.content.fileName,
        size: this.forwardData.content.fileSize,
        mainUrl: this.forwardData.content.mediaUrl,
        thumbUrl: this.forwardData.content.thumbUrl,
        encryptKey: this.forwardData.content.enc_dec_key,
        width: this.forwardData.content.width,
        height: this.forwardData.content.height,
        mediaTime: this.forwardData.content.mediaTime * 1
      };
      if (this.forwardData.type === 89) {
        msgbase.preImgPath = this.forwardData.content.preImgPath;
        msgbase.vedioFileName = this.forwardData.content.videoFileName;
        msgbase.vedioWideth = this.forwardData.content.videoWidth * 1;
        msgbase.vedioHight = this.forwardData.content.videoHeight * 1;
        msgbase.vedioLenth = this.forwardData.content.videoLength * 1;
        msgbase.vedioSize = this.forwardData.content.videoSize * 1;
        msgbase.videoFileName = this.forwardData.content.videoFileName;
        msgbase.videoWidth = this.forwardData.content.videoWidth * 1;
        msgbase.videoHeight = this.forwardData.content.videoHeight * 1;
        msgbase.videoLength = this.forwardData.content.videoLength * 1;
        msgbase.videoSize = this.forwardData.content.videoSize * 1;
      }
      if (this.forwardData.type === 9) {
        msgbase.desc = this.forwardData.content.description; // URL描述信息
        msgbase.title = this.forwardData.content.title; // URL标题
        msgbase.img = this.forwardData.content.img; // URL图标
      }
      Object.assign(msgbase, this.commonParam);
      console.log(msgbase, '文本--分享收藏--入参');
      this.$Service.chat.sendMessage(
        msgbase,
        resp => {
          console.log(resp);
          msgbase.msgId = resp.msgId
          msgbase.time = resp.time
          if (resp.code === 0) postMsgToRender('insertMsgFromDDIO', msgbase);
        }
      )
    }
  }
}
</script>

<style lang="scss" scoped>
.note-forward{
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  background: #fff;
  border-radius: 4px;
  // padding: 10px;
  .forward-title{
    padding: 6px 10px;
    background: #565656;
    color: #fff;
  }
  .forward-list{
    width: 100%;
    height: 100%;
    display: flex;
    & > div{
      width: 50%;
      height: 100%;
      display: flex;
      flex-direction: column;
    }
    .left{
      display: flex;
      flex-direction: column;
      border-right: 1px solid #b5b5b5;
      .top-search{
        position: relative;
        padding: 10px 10px;
        .icon-sousuo{
          position: absolute;
          top: 17px;
          left: 14px;
        }
        input{
          width: 100%;
          height: 30px;
          outline: #fff;
          border: 1px solid #d0d0d0;
          border-radius: 4px;
          background: #d0d0d0;
          padding: 0 23px 0 24px;
          &:focus{
            background: #fff;
            &::-webkit-input-placeholder{
              color: #fff;
            }
            &+.icon-guanbi{
              display: block;
            }
          }
        }
      }
      .left-list{
        overflow-y: auto;
        height: 100%;
        .contact-ype-title{
          padding: 5px 10px;
          background: #e0e0e0;
        }
      }
    }
    .right{
      display: flex;
      flex-direction: column;
      .right-top{
        display: flex;
        justify-content: space-between;
        padding: 10px;
        i{
          cursor: pointer;
        }
      }
      .selected-list{
        overflow-y: auto;
        height: 100%;
      }
      .right-footer{
        width: 100%;
        padding: 10px;
        display: flex;
        justify-content: flex-end;
        button{
          width: 70px;
          height: 25px;
          border-radius: 4px;
          outline: none;
          border: none;
          cursor: pointer;
          margin-left: 10px;
        }
        button:first-child{
          color: #fff;
        }
        button:last-child{
          color: #888888;
        }
        .forward-ban{
          background: #7575756e;
          cursor: not-allowed;
        }
        .forward-ok{
          background: #565656;
        }
      }
    }
  }
}
</style>



