<template>
  <div class="note-type-file">
    <div class="tiem-file">
      <div class="file-icon">
        <img :src="fileImg(content.fileName)">
      </div>
      <div class="file-detail">
        <div>{{content.fileName}}</div>
        <div class="file-process" v-if="hasLoad">
          <p class="process-box">
            <span ref="process" :style="{width:progess}"></span>
          </p>
        </div>
        <div>{{getFileSize(content.fileSize)}}</div>
      </div>
    </div>
  </div>
</template>
<script>
import config from './../common/config.js';
export default {
  props: {
    content: Object,
    progess: Number,
    hasLoad: Boolean
  },
  methods: {
    // 获取文件大小
    getFileSize(size) {
      return size > 1024 * 1024
        ? (Math.round(size * 100 / (1024 * 1024)) / 100).toString() + 'MB'
        : (Math.round(size * 100 / 1024) / 100).toString() + 'KB';
    },
    // 获取对应文件的icon
    fileImg(fileName) {
      // console.log('000----0000----', fileName);
      let suffix = fileName.split('.').pop();
      if (config.fileImg[suffix] === undefined) {
        return config.fileImg.default
      } else {
        return config.fileImg[suffix]
      }
    }
  }
}
</script>
<style lang="scss" scoped>
.note-type-file{
  width: 100%;
  .tiem-file{
    display: flex;
    .file-icon{
      img{
        width: 50px;
        height: 60px;
      }
    }
    .file-detail{
      width: 100%;
      padding: 0 10px;
      overflow: hidden;
      .file-process {
        height: 12px;
        display: flex;
        align-items: center;
        .process-box {
          height: 4px;
          width: 117px;
          background: #bdbdbd;
          margin-right: 10px;
          display: flex;
          span {
            width: 0px;
            height: 4px;
            background: #78c06d;
          }
        }
      }
      div{
        margin-bottom: 6px;
      }
      div: first-child{
        text-overflow: ellipsis;
        white-space: nowrap;
      }
      div:last-child{
        font-size: 12px;
        color: #b1b1b1;
      }
    }
  }
}
</style>



