<template>
  <div class="class-list">
    <div class="search">
      <i class="iconfont icon-sousuo"></i>
      <input
        type="text"
        :placeholder="$t('favorites.searchText')"
        v-model="keyWords"
        @input="searchFavorite"
      >
    </div>
    <div class="creater-favorite">
      <button class="creater-btn" @click="isSelectType = true">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{{$t('favorites.create')}}</button>
      <i class="iconfont icon-icon_xie"></i>
      <add-note
        class="select-type"
        v-show="isSelectType"
        @is-editBox="$emit('is-editBox')"
        @close-select-type="isSelectType = false"
      ></add-note>
    </div>
    <div
      v-for="(item, index) in classList"
      :key="index"
      class="list-item"
      :style="{background: index===activeIndex?'#c5c5c5':''}"
      @click="selectItem(item, index)"
    >
      <div class="item-icon"><i class="iconfont" :class="item.icon"></i></div>
      <div>{{item.title}}</div>
    </div>
  </div>
</template>
<script>
import AddNote from './AddNote.vue';
export default {
  components: {
    AddNote
  },
  data() {
    return {
      isSelectType: false,
      keyWords: '',
      activeIndex: 0,
      classList: [
        {title: this.$t('favorites.allFavorites'), icon: 'icon-yingyong', type: 0},
        {title: this.$t('favorites.text'), icon: 'icon-wenben', type: 2},
        {title: this.$t('favorites.file'), icon: 'icon-wenjian', type: 6},
        {title: this.$t('favorites.image'), icon: 'icon-tupian', type: 5},
        {title: this.$t('favorites.audio'), icon: 'icon-Voice', type: 3},
        {title: this.$t('favorites.video'), icon: 'icon-wenjian2', type: 89},
        {title: this.$t('favorites.link'), icon: 'icon-lianjie', type: 9}
      ]
    }
  },
  watch: {
    isSelectType() {
      this.isSelectType ? document.addEventListener('click', this.closeSelecType) : document.removeEventListener('click', this.closeSelecType)
    }
  },
  methods: {
    // 添加关闭事件
    closeSelecType(e) {
      let createrButton = document.getElementsByClassName('creater-btn')[0];
      if (!createrButton.contains(e.target)) this.isSelectType = false
    },
    // 选择添加类型
    selectItem(item, index) {
      this.$emit('class-title', item.title, item.type)
      this.activeIndex = index;
      this.keyWords = ''
    },
    searchFavorite() {
      if (this.keyWords) {
        this.$emit('class-title', this.$t('favorites.searchResult'));
        this.$Service.enterprise.searchNote(
          {
            beginID: '0',
            offset: 0,
            offsetFlag: 0,
            type: 3,
            key: this.keyWords
          },
          resp => {
            console.log(resp);
            if (resp.code === 0) {
              this.$store.commit('SET_SEARCHLIST', resp.infos)
            } else {
              this.$toast.tip({message: this.$t('favorites.searchFail')})
            }
          }
        )
      } else {
        this.$emit('class-title', this.classList[this.activeIndex].title, this.activeIndex)
      }
    }
  }
};
</script>
<style lang="scss" scoped>
.class-list{
  width: 180px;
  height: 100%;
  user-select: none;
  background: #ececec;
  .search{
    position: relative;
    padding: 10px 10px;
    .icon-sousuo{
      position: absolute;
      top: 17px;
      left: 14px;
    }
    input{
      width: 100%;
      height: 30px;
      outline: #fff;
      border: 1px solid #d0d0d0;
      border-radius: 4px;
      background: #d0d0d0;
      padding: 0 23px 0 24px;
      &:focus{
        background: #fff;
        &::-webkit-input-placeholder{
          color: #fff;
        }
        &+.icon-guanbi{
          display: block;
        }
      }
    }
  }
  .creater-favorite{
    padding: 20px 20px 30px 20px;
    position: relative;
    .icon-icon_xie{
      position: absolute;
      top: 32px;
      left: 53px;
      font-size: 13px;
    }
    button{
      width: 100%;
      height: 35px;
      border-radius: 4px;
      outline: none;
      border: none;
      background: #fff;
      cursor: pointer;
      &:hover{
        background: #f5f5f5;
      }
    }
    .select-type{
      position: absolute;
      width: 140px;
      top: 65px;
      padding: 10px 0;
      &::before {
        position: absolute;
        top: -6px;
        right: 59px;
        content: "";
        border-left: 7px solid transparent;
        border-right: 7px solid transparent;
        border-bottom: 6px solid #fff;
      }
    }
  }
  .list-item{
    display: flex;
    align-items: center;
    height: 40px;
    padding: 0 23px;
    cursor: pointer;
    .item-icon{
      margin-right: 8px;
      padding-top: 4px;
    }
    &:hover{
      background: #dedede;
    }
  }
}
</style>



