// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import store from './store'
import router from './router'
import Router from 'vue-router'
import iconfont from './../static/iconfont/iconfont.css'
import wui from './components/common/index.js'
import Viewer from 'v-viewer'
import 'viewerjs/dist/viewer.css'
import './mock/client'
import i18n from './assets/common/i18n';

// 捕获路由报错
const originalPush = Router.prototype.push
Router.prototype.push = function push(location) {
  return originalPush.call(this, location).catch(err => err)
}

Vue.config.productionTip = false;
Vue.use(iconfont);
Vue.use(wui);
Vue.use(Viewer);
Viewer.setDefaults({
  Options: {
    'inline': true,
    'button': true,
    'navbar': true,
    'title': true,
    'toolbar': true,
    'tooltip': true,
    'movable': true,
    'zoomable': true,
    'rotatable': true,
    'scalable': true,
    'transition': true,
    'fullscreen': true,
    'keyboard': true,
    'url': 'data-source',
    'initialViewIndex': 0
  }
});

let agent = navigator.userAgent.toLowerCase();
let dbPath = null;
// 判断mock环境
console.log('mock environment:', agent);
if (agent.indexOf('electron') !== -1) {
  if (!window.require) {
    window.require = window.nodeRequire;
  }
  dbPath = window.require('electron').remote.getGlobal('dbPath') + 'notedata/';
  console.log('dbPath:', dbPath);
  Vue.prototype.$dialogs = window.require('electron').remote.dialog;
  Vue.prototype.$shell = window.require('electron').shell;
  Vue.prototype.$ipc = window.require('electron').ipcRenderer;
  Vue.prototype.$fs = window.require('fs');
  let service = window.require('electron').remote.getGlobal('service');
  i18n.locale = service.config.getLanguage();
}
// 绑定vue属性
// Vue.prototype.$dbPath = dbPath;

/* eslint-disable no-new */
new Vue({
  el: '#app',
  store,
  router,
  i18n,
  components: { App },
  template: '<App/>'
})



