<template>
  <div class="modal" v-if="isShowModal">
    <div class="modal-background" @click="$emit('close')">
    </div>
    <div class="modal-card" :style="cardSize">
      <div class="modal-title" v-if="isTitle">
        <!-- <span>来自:{{title}}</span> -->
        <span>{{details}}</span>
        <!-- <i class="iconfont icon-guanbi" @click="$emit('close')"></i> -->
      </div>
      <slot></slot>
    </div>
    <div v-if="isCloseBtn" class="close" @click="$emit('close')">
      <i class="iconfont icon-guanbi1"></i>
    </div>
  </div>
</template>
<script>
import i18n from '../../../assets/common/i18n';
export default {
  name: 'wModal',
  data() {
    return {
      details: i18n.t('favorites.textDetails')
    }
  },
  props: {
    isShowModal: {
      type: Boolean,
      default: false
    },
    cardSize: String | Object,
    title: String,
    isTitle: Boolean,
    isCloseBtn: Boolean
  }
}
</script>
<style lang="scss" scoped>
.modal{
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  .modal-background{
    position: absolute;
    top: 0;
    right: 0;
    z-index: -1;
    width: 100%;
    height: 100%;
  }
  .modal-card{
    overflow: hidden;
    display: flex;
    flex-direction: column;
    border-radius: 4px;
    .modal-title{
      background: #fff;
      padding: 10px 10px;
      display: flex;
      color: #000;
      justify-content: space-between;
      border-bottom: 1px solid #b5b5b5;
      i{
        cursor: pointer;
      }
    }
  }
  .close{
    background-color: rgba(0, 0, 0, 0.5);
    border-radius: 50%;
    cursor: pointer;
    height: 80px;
    overflow: hidden;
    position: absolute;
    right: -40px;
    top: -40px;
    transition: background-color 0.15s;
    width: 80px;
    .icon-guanbi1{
      position: relative;
      top: 45px;
      right: -20px;
      color: #fff;
      font-size: 10px;
    }
    &:hover{
      background: rgba(0, 0, 0, 0.8);
      cursor: pointer;
    }
  }
}
</style>



