<template>
  <div class="note-type-text">
    <div class="text-content" v-html="textContent()"></div>
  </div>
</template>
<script>
export default {
  props: {
    content: Object
  },
  methods: {
    textContent() {
      let txt = this.content.body.substr(0, 150).replace(/\n/g, '<br/>');
      txt += this.content.body.length > 150 ? '...' : '';
      return txt
    }
  }
}
</script>
<style lang="scss" scoped>
.note-type-text{
  width: 100%;
}
.text-content {
  width: 100%;
  word-break: break-word;
}
</style>



