export function editBoxGetFocus(el) {
  el.focus();
  var range = document.createRange();
  range.selectNodeContents(el);
  range.collapse(false);
  var sel = window.getSelection();
  sel.removeAllRanges();
  sel.addRange(range);
}

export function encryptionPassword() {
  let num = '';
  for (let i = 0; i < 16; i++) {
    num += Math.floor(Math.random() * 10);
  }
  return num;
};

// 时间戳格式化标准时间
export function getLocalTime(timestamp) {
  let ttp = timestamp.toString().substr(0, 10);
  let date = new Date(parseInt(ttp) * 1000).toLocaleString().replace(/:\d{1,2}$/, ' ');
  return date.split(' ')[0]
}



