<template>
  <div class="video-preview">
    <video width="auto" height="auto" controls ref="video">
      <!-- <source type="video/mp4" ref="source"> -->
    </video>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
export default {
  computed: {
    ...mapGetters(['http_head'])
  },
  props: {
    content: Object,
    videoPath: String
  },
  data() {
    return {
      videoPlayPath: this.videoPath
    }
  },
  watch: {
    videoPath() {
      this.$nextTick(() => {
        this.$refs.video.src = 'file://' + this.videoPath;
      })
      console.log('pcVideoPath-hasChange', this.videoPath);
    }
  },
  mounted() {
    this.$nextTick(() => {
      this.$refs.video.src = 'file://' + this.videoPath;
    })
  },
  methods: {}
}
</script>

<style lang="scss" scoped>
.video-preview{
  max-width: 100%;
  max-height: 100%;
  display: flex;
  video{
    background: #000;
    max-width: 100%;
    max-height: 100%;
  }
}
</style>



