const getters = {
  user_account: state => state.note.account,
  note_list: state => state.note.noteList,
  search_list: state => state.note.searchList,
  user_imgPath: state => state.note.userImgPath,
  user_filePath: state => state.note.userFilePath,
  user_audioPath: state => state.note.userAudioPath,
  user_videoPath: state => state.note.userVideoPath,
  http_head: state => state.note.httpHead
};
export default getters;



