<template>
  <div class="create-favorite-bubble">
    <!-- <div class="bubble-title">选择要创建的类型</div>
    <div></div> -->
    <div
      v-for="(type, index) in creatType"
      :key="index"
      class="type-item"
    >
      <label @click="selectType(type, index)" :for="type">{{type}}</label>
      <input v-if="index > 0" type="file" :id="type" @change="uploadFile($event)"/>
    </div>
  </div>
</template>
<script>
import { mapGetters } from 'vuex'
import noteMixin from './../common/noteMixin.js';
import { encryptionPassword } from './../common/util.js'
export default {
  mixins: [noteMixin],
  data() {
    return {
      creatType: [this.$t('favorites.text'), this.$t('favorites.file')]
    }
  },
  computed: {
    ...mapGetters(['user_account'])
  },
  methods: {
    // 选择要创建的收藏类型
    selectType(item, index) {
      console.log(item, index, '选择创建的收藏类型');
      if (index === 0) this.$emit('is-editBox');
      this.$emit('close-select-type');
    },
    // 上传文件
    uploadFile(e) {
      console.log(e, 'select file $event');

      var file = e.target.files[0];
      var reader = new FileReader();
      reader.onload = e => {
        console.log(e, 'reader onload file');
      };
      reader.readAsDataURL(file);

      let filePath = e.target.files[0].path;
      let fileName = e.path[0].files[0].name;
      let fileSize = e.path[0].files[0].size;
      let ImgReg = /(?:jpg|gif|png|jpeg|svg)$/;
      let videoReg = /(?:rm|rmvb|wmv|avi|mp4|3gp|mkv)$/;
      let audioReg = /(?:mp3|wav|ape|cd|aiff|au)$/;
      let addNoteParam = {
        type: String,
        content: {},
        title: fileName
      };
      if ((fileSize / 1024 / 1024).toFixed(2) > 100) {
        this.$toast.tip({message: this.$t('favorites.overSize')});
        return
      };
      if (ImgReg.test(fileName)) addNoteParam.type = 5;
      else if (audioReg.test(fileName)) addNoteParam.type = 3;
      else if (videoReg.test(fileName)) addNoteParam.type = 89;
      else {
        addNoteParam.type = 6
      };
      console.log(filePath, '本地文件路径');
      addNoteParam.content = {
        fileSize: fileSize,
        fileName: fileName,
        mediaUrl: filePath,
        thumbUrl: ''
      };
      this.addNote(addNoteParam, false);
      this.$Service.file.uploadFile(
        {localPath: filePath}, // encryptKey: encryptionPassword()
        resp => {
          console.log(resp, '上传文件回调');
          if (resp.code === 0) {
            let fileData = JSON.parse(resp.json); // files[0].url
            addNoteParam.content.mediaUrl = fileData.files[0].url;
            // this.$store.commit('SET_NOTE', {flag: 1, mainUrl: fileData.files[0].url});
            this.addNote(addNoteParam, true);
          }
        },
        pro => {
          console.log(pro, '上传进度');
        }
      )
    }
  }
}
</script>
<style lang="scss" scoped>
.create-favorite-bubble{
  width: 100%;
  background: #fff;
  border-radius: 4px;
  .bubble-title{
    color: #fff;
    padding: 5px 10px;
    background: #565656
  }
  .type-item{
    label{
      width: 100%;
      height: 100%;
      padding: 5px 10px;
      cursor: pointer;
      display: inline-block;
    }
    input{
      display: none;
    }
    &:hover{
      background: #b5b5b5;
    }
  }
}
</style>



