<template>
  <div class='contant-list'>
    <div
      class='contant-item'
      v-for='(item, index) in noteList'
      :key='index'
      @click='selectItem(item, index)'
      @contextmenu.prevent='openContextmenuList(item, index, $event)'
    >
      <div class='item-info'>
        <component :is='currentViews(item.type)' :content='getContentInfo(item, index)' :hasLoad='item.hasLoad||false' :progess='item.progess||0'></component>
      </div>
      <!-- 收藏用户详情收藏用户详情 -->
      <user-info :item='item'></user-info>
    </div>
    <w-menu
      :contextmenuList='menuList'
      :isContextmenu='isContextmenu'
      :contextmenuPos='contextmenuPos'
      :parenteItem='parenteItem'
      @select-item='contextmenuItem'
      @close='isContextmenu=false'
    ></w-menu>
    <w-modal
      :cardSize='isNotePreview?cardSizePreview:cardSize'
      :isShowModal='isShowModal'
      :isTitle='isWmodalTitle'
      :title='wModalTitle'
      :isCloseBtn='true'
      @close='[isShowModal = false, isNotePreview = false]'
    >
      <component
        class='note-preview'
        v-if='isNotePreview'
        :is='currentPreviews(previewItem.type)'
        :content='previewItem.content'
        :audioPath="previewItem.type === 3 ? decryptAudio : ''"
        :videoPath="previewItem.type === 89 ? decryptVideo : ''"
      ></component>
      <note-forward v-else @close='isShowModal = false' :isTitle='false' :forwardData='forwardData'></note-forward>
    </w-modal>
  </div>
</template>
<script>
import { mapGetters } from 'vuex';
import config from './../common/config';
import NoteForward from './../forward/index.vue';
import UserInfo from './UserInfo.vue';
import AMR from './../preview/amrnb';
import i18n from '../../assets/common/i18n';
// 预览模板
import { previewTxt, previewVideo, previewAudio } from './../preview/index.js';
// 类型模板
import {
  TypeTxt,
  TypeImg,
  TypeAudio,
  TypeVideo,
  TypeUrl,
  TypeFile
} from './../note/index.js';
import { Promise } from 'q';
import { resolve } from 'url';
export default {
  components: {
    NoteForward,
    UserInfo,
    TypeTxt,
    TypeImg,
    TypeAudio,
    TypeVideo,
    TypeUrl,
    TypeFile,
    previewTxt,
    previewVideo,
    previewAudio
  },
  data() {
    return {
      menuList: [this.$t('favorites.delete'), this.$t('favorites.share'), this.$t('favorites.save')],
      isContextmenu: false,
      parenteItem: null,
      contextmenuPos: {
        top: '',
        left: ''
      },
      cardSize: {
        width: '500px',
        height: '440px'
      },
      cardSizePreview: {
        width: '',
        height: ''
      },
      isShowModal: false,
      forwardData: Object,
      isNotePreview: false,
      previewItem: Object,
      wModalTitle: '',
      isWmodalTitle: false,
      images: [],
      isImgPreview: false,
      pcAudioPath: '',
      decryptAudio: '',
      decryptVideo: '',
      num: 1
    };
  },
  watch: {
    noteList() {
      console.log('vuex变化了' + this.num + '次');
      this.num++;
    }
  },
  props: {
    noteList: Array,
    isSearch: Boolean
  },
  computed: {
    ...mapGetters(['note_list', 'search_list', 'user_account', 'http_head', 'user_imgPath', 'user_videoPath', 'user_filePath', 'user_audioPath']),
    computedNoteList() {
      console.log('this.noteList', this.noteList);
      return this.noteList
    }
  },
  methods: {
    // 获取需要展示的源数据     处理特殊数据源信息
    getContentInfo(item, index) {
      // console.log('getContentInfo>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>', index)
      let [content, type] = [item.content, item.type]
      switch (type) {
        case 5: { // 图片
          if (!content.enc_dec_key) {
            return [this.http_head + content.mediaUrl];
          } else {
            if (item.imgArr) return item;
            this.$set(item, 'imgArr', [])
            this.loadImg(item)
            return item
          }
        }
        case 28: { // 九图
          let urls = [];
          content.serverUrl.forEach(element => {
            urls.push(this.http_head + element.mediaUrl);
            // this.loadImg({content: element});
          });
          return {'imgArr': urls};
        }
        case 89: { // 微视频
          if (item.imgUrl) return item.imgUrl
          this.$set(item, 'imgUrl', null)
          this.loadMicroVideo(item)
          return item.imgUrl
        }
        default: {
          console.log('default:::', type, this.num)
          return content;
        }
      }
    },
    /**
     * 加载微视频封面
     */
    loadMicroVideo(item) {
      console.log('loadMicroVideo:::item', item, this.num)
      // 获取封面路径
      this.getImgPath(item.content.preImgPath).then(imgUrl => {
        if (this.$Service.file.fileOperate({filepath: imgUrl, type: 0})) {
          // 文件存在时，直接设置
          this.$set(item, 'imgUrl', imgUrl)
        } else {
          // 文件不存在时，配置下载参数，进行下载
          let downloadImageParam = {
            targetId: item.targetId,
            url: item.content.preImgPath
          }
          console.log('downloadImageParam:::', downloadImageParam)
          this.$Service.file.downloadImage(downloadImageParam, resp => {
            console.log('downloadImage::::resp', resp, 'target:', item.content)
            if (resp.code === 0) {
              // 缓存文件路径
              let cachePath = `${this.user_imgPath}${resp.imgName.substr(0, resp.imgName.lastIndexOf('/'))}/cache`
              // 解密文件路径
              let destPath = `${cachePath}/${resp.imgName.substr(resp.imgName.lastIndexOf('/') + 1)}`
              // 解密文件
              this.decryptFiles(item.content, destPath, resp.imgName).then(() => {
                this.$set(item, 'imgUrl', destPath);
              })
            } else {
              console.log('downloadMicroVideo fail', resp.code);
            }
          })
        }
      })
    },
    loadImg(item) {
      let content = item.content
      console.log('图片信息:', content);
      // 判断是否存在缩略图路径以及 本地是否存在
      if (content.thumbPath && this.$Service.file.fileOperate({filepath: content.thumbPath, type: 0})) {
        let imgArrObject = Object.assign({}, item.imgArr[0]);
        this.$set(imgArrObject, 'thumb', `file://${content.thumbPath}`);
        this.$set(item, 'imgArr', [imgArrObject]);
        // this.$set(item, 'imgArr', [`file://${content.thumbPath}`]);
      } else {
        // 若不存在，则进行相关下载处理
        // 通过文件名称，从服务器获取一个本地的唯一文件名
        this.getImgPath(content.thumbUrl).then(imgUrl => {
          // 判断图片是否存在 存在直接加载
          if (this.$Service.file.fileOperate({filepath: imgUrl, type: 0})) {
            // this.$set(item, 'imgArr', [`file://${imgUrl}`])
            let imgArrObject = Object.assign({}, item.imgArr[0]);
            this.$set(imgArrObject, 'thumb', `file://${imgUrl}`);
            this.$set(item, 'imgArr', [imgArrObject]);
          } else {
            // 若不存在，则判断是否存在base64图片，若不存在则下载文件
            if (content.thumBbinary) {
              // this.$set(item, 'imgArr', ['data:image/jpeg;base64,' + content.thumBbinary])
              let imgArrObject = Object.assign({}, item.imgArr[0]);
              this.$set(imgArrObject, 'thumb', 'data:image/jpeg;base64,' + content.thumBbinary);
              this.$set(item, 'imgArr', [imgArrObject]);
              return
            }
            this.downloadImg(item, false)
          }
        })
      }
      // 判断是否存在缩略图路径以及 本地是否存在
      if (content.mainPath && this.$Service.file.fileOperate({filepath: content.mainPath, type: 0})) {
        let imgArrObject = Object.assign({}, item.imgArr[0]);
        this.$set(imgArrObject, 'main', `file://${content.mainPath}`);
        this.$set(item, 'imgArr', [imgArrObject]);
        // this.$set(item, 'imgArr', [`file://${content.thumbPath}`]);
      } else {
        // 若不存在，则进行相关下载处理
        // 通过文件名称，从服务器获取一个本地的唯一文件名
        this.getImgPath(content.mediaUrl).then(imgUrl => {
          // 判断图片是否存在 存在直接加载
          if (this.$Service.file.fileOperate({filepath: imgUrl, type: 0})) {
            // this.$set(item, 'imgArr', [`file://${imgUrl}`])
            let imgArrObject = Object.assign({}, item.imgArr[0]);
            this.$set(imgArrObject, 'main', `file://${imgUrl}`);
            this.$set(item, 'imgArr', [imgArrObject]);
          } else {
            // 若不存在，则判断是否存在base64图片，若不存在则下载文件
            if (content.thumBbinary) {
              // this.$set(item, 'imgArr', ['data:image/jpeg;base64,' + content.thumBbinary])
              let imgArrObject = Object.assign({}, item.imgArr[0]);
              this.$set(imgArrObject, 'main', 'data:image/jpeg;base64,' + content.thumBbinary);
              this.$set(item, 'imgArr', [imgArrObject]);
              return
            }
            this.downloadImg(item, true)
          }
        })
      }
      // if (content.mediaUrl && this.$Service.file.fileOperate({filepath: content.mediaUrl, type: 0})) {
      //   item.imgArr.push(`file://${content.mediaUrl}`);
      // } else {
      //   this.getImgPath(content.mediaUrl).then(imgUrl => {
      //     // 判断图片是否存在 存在直接加载
      //     if (this.$Service.file.fileOperate({filepath: imgUrl, type: 0})) {
      //       item.imgArr.push(`file://${imgUrl}`);
      //     } else {
      //       this.downloadImg(item, true);
      //     }
      //   })
      // }
      // // 通过文件名称，从服务器获取一个本地的唯一文件名
      // this.getImgPath(content.mediaUrl).then(imgUrl => {
      //   // 判断图片是否存在 存在直接加载
      //   if (this.$Service.file.fileOperate({filepath: imgUrl, type: 0})) {
      //     this.$set(item, 'imgArr', [`file://${imgUrl}`])
      //   } else {
      //     // 若不存在，则判断是否存在base64图片，若不存在则下载文件
      //     // if (content.thumBbinary) {
      //     //   this.$set(item, 'imgArr', ['data:image/jpeg;base64,' + content.thumBbinary])
      //     //   return
      //     // }
      //     this.downloadImg(item)
      //   }
      // })
    },
    getImgPath(sourcePath) {
      return new Promise((resolve, reject) => {
        let getNameByUrlCb = resp => { // 通过图片名称获取它的url
          console.log('getImgPath:::getNameByUrlCb', resp, 'sourcePath:::', sourcePath)
          if (resp.result) {
            let imgLastName = resp.result.lastIndexOf('/') // imgLastName 为图片后缀名称
            let imgLocation = resp.result.substr(0, imgLastName) // 图片所在位置
            let imgUrls = `${this.user_imgPath}${imgLocation}/cache${resp.result.substr(imgLastName)}` // 图片完整路径
            resolve(imgUrls)
          }
        }
        this.$Service.file.getNameByUrl({ url: sourcePath }, getNameByUrlCb)
      })
    },
    // 点击打开右键菜单列表
    openContextmenuList(item, index, evt) {
      console.log('右键菜单项：', item, index);
      this.contextmenuPos.left = evt.clientX + 'px';
      this.contextmenuPos.top = evt.clientY + 'px';
      this.parenteItem = { parenteIndex: index, item: item };
      this.menuList = []
      if (item.type === 28) {
        this.menuList.push(this.$t('favorites.delete'));
      } else if (item.type === 2 || item.type === 9) {
        this.menuList.push(this.$t('favorites.delete'), this.$t('favorites.share'));
      } else {
        this.menuList.push(this.$t('favorites.delete'), this.$t('favorites.share'), this.$t('favorites.save'));
      }
      this.isContextmenu = true;
    },
    // 选中右键菜单列表中的条目
    contextmenuItem(pt, item, index) {
      console.log(pt, item, index);
      switch (index) {
        case 0: {
          this.$alert.confirm({
            confirmText: i18n.t('favorites.deleteConfirm'),
            title: i18n.t('favorites.warn') + '!',
            onConfirm: () => {
              this.deleteNote(pt, item, index);
            }
          });
          break;
        }
        case 1: {
          this.isShowModal = true;
          console.log('forwardData::::', pt.item)
          this.forwardData = pt.item;
          break;
        }
        case 2: {
          this.downloadFile(pt.item, 1);
          break;
        }
      }
    },
    // 删除收藏
    deleteNote(pt, item, index) {
      this.$Service.enterprise.delNote([pt.item.id], resp => {
        console.log(resp);
        if (resp.code === 0) {
          if (this.isSearch) {
            this.$delete(this.search_list, pt.parenteIndex);
          } else {
            for (let i = 0; i <= this.note_list.length; i++) {
              if (pt.item.id === this.note_list[i].id) {
                this.$delete(this.note_list, i);
                break;
              }
            }
          }
          this.$toast.tip({message: i18n.t('favorites.deleteSuccess') });
        } else {
          this.$toast.tip({message: i18n.t('favorites.deleteFail')});
        }
      });
    },
    downloadFile(item, flag, isFile = true) {
      console.log(item, '文件参数');
      let type = item.type;
      let [fileName, fileUrl, filePath] = this.getFileConfig(item);
      console.log('文件参数->', item);
      if (type === 6 || type === 89) { // 下载文件类型时时判断是否有本地缓存
        // 文件本地路径
        let fileLocalPath = filePath + 'cache/' + fileName;
        console.log('文件本地路径->', fileLocalPath);
        // 判断是本地是否存在该文件
        let hasFile = this.$Service.file.fileOperate({
          filepath: fileLocalPath,
          type: 0
        });
        // 当本地存在该文件， 打开该文件夹
        if (hasFile && flag === 0) {
          switch (type) {
            case 6:
              this.$shell.showItemInFolder(fileLocalPath);
              break;
            case 89:
              this.decryptVideo = fileLocalPath;
              break;
            default: break;
          }
          return;
        }
      }
      // 判断是否存在本地数据库路径(直接创建，若存在则不处理，不存在则创建)
      if (flag === 0) {
        this.$Service.file.fileOperate({
          filepath: filePath,
          type: 1
        })
      }
      let localSavePath = null; // 本地存储路径
      let result = '' // 文件名称
      // 判断文件是否 加密  配置文件名称
      if (!item.content.enc_dec_key) {
        result = fileName;
      } else {
        result = this.getFileName(fileUrl);
      }
      // 下载文件状态，设置本地存储路径
      if (flag === 0) {
        localSavePath = filePath + result;
      }
      // 另存为状态，获取本地存储路径
      if (flag === 1) {
        // let audioPath = decryptFiles.substr(0, decryptFiles.lastIndexOf('.') + 1) + 'wav';
        localSavePath = this.$dialogs.showSaveDialog({
          defaultPath: fileName
        });
      }
      // 判断本地存储路径是否合法
      if (localSavePath === undefined) {
        return;
      }
      console.log(localSavePath, '文件保存的路径');
      let downloadFileParam = { // 配置下载文件参数
        localPath: localSavePath,
        url: this.http_head + fileUrl
      };
      console.log('downloadFileParam::::', downloadFileParam);
      if (type === 6) { // 设置文件下载状态
        this.$set(item, 'hasLoad', true);
        this.$set(item, 'progess', '0px');
      }
      this.$Service.file.downloadFile(
        downloadFileParam,
        resp => {
          console.log(resp, '下载文件返回');
          if (resp.code !== 0) {
            if (type === 6) {
              this.$set(item, 'hasLoad', false);
            }
            if (resp.code === -16777206) {
              this.$toast.tip({ message: i18n.t('favorites.overdue') });
            } else {
              console.log('下载失败：', resp.code);
              this.$toast.tip({ message: i18n.t('favorites.downloadFail') });
            }
            return;
          };
          let reNamePath = `${resp.localPath.split('.')[0]}temp`;
          console.log(`reNamePath::: ${reNamePath}`)
          let fileOperateParam = {
            filepath: resp.localPath,
            type: 3,
            newName: reNamePath
          }
          if (!item.content.enc_dec_key) {
            console.log('未加密');
            if (type === 6) {
              this.$shell.showItemInFolder(localSavePath);
            } else if (type === 3) {
              this.decryptAudio = localSavePath;
            }
          } else {
            let cachePath = `${filePath}cache/`;
            let fileCachePath = cachePath + fileName;
            if (flag === 1 || type === 6 || type === 89) {
              this.$Service.file.fileOperate(fileOperateParam);
            };
            this.$Service.file.fileOperate({
              filepath: cachePath,
              type: 1
            });
            if (flag === 1) {
              fileCachePath = localSavePath
            }
            let realName = '';
            if (flag === 1 || type === 6 || type === 89) {
              realName = reNamePath;
            } else {
              realName = resp.localPath
            }
            console.log('realName:', realName);
            this.decryptFiles(item.content, realName, fileCachePath, isFile).then(() => {
              if (flag === 1 || type === 6) {
                this.$set(item, 'hasLoad', false);
                this.$Service.file.fileOperate({
                  filepath: reNamePath,
                  type: 2
                });
                this.$shell.showItemInFolder(fileCachePath);
              }
              if (type === 89) {
                this.decryptVideo = fileCachePath;
                this.$Service.file.fileOperate({
                  filepath: reNamePath,
                  type: 2
                });
              }
              if (flag !== 1 && type === 3) {
                this.decodeWav(fileCachePath);
              }
            })
          }
        },
        proResp => {
          console.log(proResp, '文件下载进度返回');
          if (type === 6) {
            this.$set(item, 'progess', proResp.progess * 1.17 + 'px');
          }
        }
      );
    },
    downloadImg(item, isHD = false) {
      let messageObject = item.content
      let downloadImageParam = {
        targetId: item.targetId,
        url: isHD ? messageObject.mediaUrl : messageObject.thumbUrl
      };
      console.log('downloadImageParam', downloadImageParam);
      let downloadImageProcess = process => { // 图片下载进度
        console.log('progess>>>>>>>>>', process.progess)
        if (process.progess === 100) {
          // this.$set(item, 'hasLoad', false)
          console.log('img downloaded->', downloadImageParam.url)
        }
      };
      // this.$set(item, 'hasLoad', true)
      this.$Service.file.downloadImage(
        downloadImageParam,
        resp => {
          console.log(`downloadImage -- code`, resp);
          if (resp.code === 0) {
            let cachePath = `${this.user_imgPath}${resp.imgName.substr(
              0,
              resp.imgName.lastIndexOf('/')
            )}/cache`;
            let createFile = this.$Service.file.fileOperate({
              filepath: cachePath,
              type: 1
            });
            console.log(`isCreateFile? ${createFile}`);
            if (createFile) {
              // 图片解密后保存的路径
              let destPath = `${cachePath}/${resp.imgName.substr(
                resp.imgName.lastIndexOf('/') + 1
              )}`;
              console.log(`imgSavePath::  ${destPath}`);
              /** 解密图片 */
              this.decryptFiles(messageObject, destPath, resp.imgName).then(decryptResp => {
                if (decryptResp.result) {
                  let imgArrObject = Object.assign({}, item.imgArr[0]);
                  this.$set(imgArrObject, isHD ? 'main' : 'thumb', `file://${destPath}`);
                  this.$set(item, 'imgArr', [imgArrObject]);
                } else {
                  let imgArrObject = Object.assign({}, item.imgArr[0]);
                  this.$set(imgArrObject, isHD ? 'main' : 'thumb', 'data:image/jpeg;base64,' + messageObject.thumBbinary);
                  this.$set(item, 'imgArr', [imgArrObject]);
                  // this.$set(item, 'imgArr', ['data:image/jpeg;base64,' + messageObject.thumBbinary])
                }
              });
            } else {
              console.log('created -- cache -- faile');
              // this.$set(item, 'imgArr', ['data:image/jpeg;base64,' + messageObject.thumBbinary])
              let imgArrObject = Object.assign({}, item.imgArr[0]);
              this.$set(imgArrObject, isHD ? 'main' : 'thumb', 'data:image/jpeg;base64,' + messageObject.thumBbinary);
              this.$set(item, 'imgArr', [imgArrObject]);
            }
          } else {
            // 图片下载失败
            console.log(`imgdownLoad fail:: ${resp.code}`);
            // this.$set(item, 'imgArr', ['data:image/jpeg;base64,' + messageObject.thumBbinary])
            let imgArrObject = Object.assign({}, item.imgArr[0]);
            this.$set(imgArrObject, isHD ? 'main' : 'thumb', 'data:image/jpeg;base64,' + messageObject.thumBbinary);
            this.$set(item, 'imgArr', [imgArrObject]);
          }
        },
        downloadImageProcess
      );
    },
    /**
    * 解密文件
    * @destPath 原图路径
    * @imgName 解密文件路径
    */
    decryptFiles(messageObject, destPath, imgName, isFile = false) {
      return new Promise((resolve, reject) => {
        let decryptFileParam = {
          encryptkey: messageObject.enc_dec_key,
          // srcpath: destPath,
          // destpath: isFile ? imgName : this.user_imgPath + imgName,
          flag: 1
        }
        if (isFile) {
          decryptFileParam.srcpath = destPath
          decryptFileParam.destpath = imgName
        } else {
          decryptFileParam.srcpath = this.user_imgPath + imgName
          decryptFileParam.destpath = destPath
        }
        console.log('decryptFileParam:::', decryptFileParam)
        let decryptFileCb = decryptFileRes => {
          console.log(`imgDecryResult::  ${decryptFileRes.result}`);
          resolve(decryptFileRes)
        }
        this.$Service.file.decryptFile(decryptFileParam, decryptFileCb)
      })
    },
    // 点击选中当前条目
    selectItem(item, index) {
      console.log('收藏item数据:::', item, index);
      let typeObj = [2, 3, 89];
      this.previewItem = item;
      switch (item.type) {
        // 查看文本
        case 2: {
          this.isWmodalTitle = true;
          this.cardSizePreview.width = '600px';
          this.cardSizePreview.height = '500px';
          break;
        }
        // 查看音频
        case 3: {
          this.isWmodalTitle = true;
          this.cardSizePreview.width = '450px';
          this.cardSizePreview.height = 'auto';
          this.decryptAudio = '';
          this.getAudio(item);
          break;
        }
        // 查看图片
        case 5: {
          // this.loadImg(item, true);
          break;
        }
        // 下载文件
        case 6: {
          this.downloadFile(item, 0);
          break;
        }
        // 打开url
        case 9: {
          this.$ipc.send('new-window-weburl', item.content.url);
          break;
        }
        // 查看视频
        case 89: {
          this.decryptVideo = '';
          this.isWmodalTitle = false;
          this.cardSizePreview = {};
          this.cardSizePreview.maxWidth = '80%';
          this.cardSizePreview.maxHeight = '95%';
          this.downloadFile(item, 0, true);
          break;
        }
      }
      if (!typeObj.includes(item.type)) return;
      if (!this.wModalTitle) {
        this.$Service.search.getUserInfo(
          { userId: item.sendUserId },
          resp => {
            if (!resp || resp.code !== 0) return
            this.wModalTitle = resp.users && resp.users.name
          }
        )
      } else {
        this.wModalTitle = item.content.name;
      }
      this.isNotePreview = true;
      this.isShowModal = true;
      this.previewItem = item;
    },
    // 文本预览模板
    currentPreviews(previewType) {
      switch (previewType) {
        case 2:
          return previewTxt;
        case 3:
          return previewAudio;
        case 89:
          return previewVideo;
      }
    },
    // 列表模板
    currentViews(type) {
      switch (type) {
        case 2:
          return TypeTxt;
        case 3:
          return TypeAudio;
        case 5:
          return TypeImg;
        case 6:
          return TypeFile;
        case 9:
          return TypeUrl;
        case 28:
          return TypeImg;
        case 89:
          return TypeVideo;
      }
    },
    /**
     * 获取音频文件
     */
    getAudio(item) {
      console.log('获取音频文件：', item);
      let cachePath = `${this.user_audioPath}cache/`;
      let fileCachePath = cachePath + item.content.fileName;
      if (item.content.hasOwnProperty('pcAudioPath') && item.content.pcAudioPath) {
        let fileOperateParam = {
          filepath: item.content.pcAudioPath,
          type: 0
        };
        let isExistFile = this.$Service.file.fileOperate(fileOperateParam);
        console.log('本地是否存在选择的音频', isExistFile);
        if (!isExistFile) {
          this.downloadFile(item, 0);
        } else {
          this.decryptFiles(item.content, item.content.pcAudioPath, fileCachePath, true).then(() => {
            this.decodeWav(fileCachePath);
          })
        }
      } else {
        let fileName = this.getFileName(item.content.mediaUrl);
        let fileOperateParam = {
          filepath: this.user_audioPath + fileName,
          type: 0
        };
        let isExistFile = this.$Service.file.fileOperate(fileOperateParam);
        console.log('本地是否存在选择的音频', isExistFile);
        if (!isExistFile) {
          this.downloadFile(item, 0);
        } else {
          this.$Service.file.fileOperate({
            filepath: cachePath,
            type: 1
          });
          this.decryptFiles(item.content, fileOperateParam.filepath, fileCachePath, true).then(() => {
            this.decodeWav(fileCachePath);
          })
        }
      }
    },
    /*
     *下载音频文件
     */
    downLoadAudioFile(item, flag) {
      console.log(item, '文件参数');
      let fileName = item.content.fileName;
      let fileUrl = item.content.mediaUrl;
      let filePath = this.user_audioPath;
      // 判断是否存在本地数据库路径(直接创建，若存在则不处理，不存在则创建)
      if (flag === 0) {
        this.$Service.file.fileOperate({
          filepath: filePath,
          type: 1
        })
      }
      let localSavePath = null; // 本地存储路径
      let result = '' // 文件名称
      // 判断文件是否 加密  配置文件名称
      if (!item.content.enc_dec_key) {
        result = fileName;
      } else {
        result = this.getFileName(item.content.mediaUrl);
      }
      // 下载文件状态，设置本地存储路径
      if (flag === 0) {
        localSavePath = filePath + result;
      }
      // 另存为状态，获取本地存储路径
      if (flag === 1) {
        localSavePath = this.$dialogs.showSaveDialog({
          defaultPath: fileName
        });
      }
      // 判断本地存储路径是否合法
      if (localSavePath === undefined) {
        return;
      }
      console.log(localSavePath, '文件保存的路径')
      let downloadFileParam = {
        localPath: localSavePath,
        url: this.http_head + fileUrl
      };
      console.log('downloadFileParam::::', downloadFileParam);
      this.$Service.file.downloadFile(
        downloadFileParam,
        resp => {
          console.log(resp, '下载文件返回');
          if (resp.code !== 0) {
            console.log('下载失败：', resp.code);
            this.$toast.tip({ message: i18n.t('favorites.downloadFail') });
            return;
          };
          this.decryptAudio = localSavePath;
          let cachePath = `${this.user_audioPath}cache/`;
          this.$Service.file.fileOperate({
            filepath: cachePath,
            type: 1
          });
        },
        proResp => {
          console.log(proResp, '文件下载进度返回');
        }
      );
    },
    /**
     * 工具函数 获取文件名称(无后缀)
     */
    getFileName(str) {
      let index = str.lastIndexOf('/');
      let path = str.substr(index + 1); // 取文件完整名称
      let fileName = path.substr(0, path.lastIndexOf('.')); // 取文件名称(无后缀)
      return fileName || path; // 处理无后缀的文件名称
    },
    /**
     * 工具函数 获取文件路径配置
     */
    getFileConfig(item) {
      let [fileName, fileUrl, filePath] = ['', '', ''];
      switch (item.type) { // 配置文件信息
        case 3:
          fileName = item.content.fileName;
          fileUrl = item.content.mediaUrl;
          filePath = this.user_audioPath;
          break;
        case 5:
          break;
        case 6:
          fileName = item.content.fileName;
          fileUrl = item.content.mediaUrl;
          filePath = this.user_filePath;
          break;
        case 28:
          break;
        case 89:
          let index = item.content.videoFileName.lastIndexOf('/');
          fileName = item.content.videoFileName.substr(index + 1);
          fileUrl = item.content.videoFileName;
          filePath = this.user_videoPath;
          break;
        default:
          fileName = item.content.fileName;
          fileUrl = item.content.mediaUrl;
          filePath = this.user_filePath;
          break;
      };
      return [fileName, fileUrl, filePath];
    },
    /**
     * 音频转格式
     */
    decodeWav(decryptFiles) {
      console.log('转换音频的格式');
      if (decryptFiles.includes('.wav')) {
        this.decryptAudio = decryptFiles;
      } else {
        let audioPath = decryptFiles.substr(0, decryptFiles.lastIndexOf('.') + 1) + 'wav';
        this.$fs.readFile(decryptFiles, (readErr, data) => {
          let wav = AMR.toWAV(new Uint8Array(data));
          this.$fs.writeFile(audioPath, wav, (err) => {
            if (err) throw err;
            console.log('It\'s saved!');
            this.decryptAudio = audioPath;
          })
        })
      }
    },
    /**
     * 更新笔记字段
     */
    editNote(item, path) {
      let editParam = Object.assign({}, item);
      this.$set(editParam.content, 'pcAudioPath', path);
      let content = JSON.stringify(item.content);
      this.$set(editParam, 'content', content);
      this.$set(editParam, 'lastChgTime', new Date().getTime() + '');
      console.log('更新笔记参数', editParam);
      this.$Service.enterprise.editNote(editParam, resp => {
        console.log('更新笔记resp', resp);
      })
    }
  }
};
</script>

<style lang='scss' scoped>
.contant-list {
  width: 100%;
  height: calc(100% - 55px);
  overflow: hidden;
  overflow-y: auto;
  padding: 0 35px;
  // display: flex;
  // flex-direction: column;
  .note-preview {
    padding: 10px;
  }
  .contant-item {
    padding: 20px 0;
    border-bottom: 1px solid #b5b5b5;
    display: flex;
    width: 100%;
    cursor: pointer;
    .item-info {
      width: 100%;
      overflow: hidden;
    }
    // .item-from{
    //   width: 200px;
    //   color: #b1b1b1;
    //   font-size: 12px;
    //   text-align: end;
    //   div{
    //     padding: 2px 0;
    //   }
    // }
  }
}
</style>



