<template>
  <div>
    <!-- <viewer :images="images">
      <img v-for="src in signImages" :src="src" :key="src" width="50">
    </viewer> -->
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
export default {
  computed: {
    ...mapGetters(['http_head'])
  },
  props: {
    images: Array
  }
}
</script>

<style lang="scss" scoped>
.img-preview{
  max-width: 100%;
  max-height: 100%;
  display: flex;
  position: relative;
  img{
    position: absolute;
    width: 100%;
    height: 100%;
  }
}
</style>



