<template>
  <div class="note-type-url">
    <div class="url-content">
      <div class="url-img">
        <img :src="urlImg">
      </div>
      <div class="url-detail">
        <div class="url-title">{{content.title}}</div>
        <div class="url-desc">{{content.description}}</div>
      </div>
    </div>
  </div>
</template>
<script>
import { mapGetters } from 'vuex';
export default {
  props: {
    content: Object
  },
  data() {
    return {
      defaultImg: require('../../assets/img/fileImg/web.png')
    }
  },
  computed: {
    urlImg() {
      return (this.content.img ? this.content.img : this.defaultImg);
    }
  },
  methods: {}
}
</script>
<style lang="scss" scoped>
.note-type-url{
  width: 100%;
  .url-content{
    display: flex;
    .url-img{
      img{
        width: 60px;
        height: 60px;
      }
    }
    .url-detail{
      width: 100%;
      padding: 0 10px;
      overflow: hidden;
      div{
        margin-bottom: 6px;
      }
      .url-title{
        text-overflow: ellipsis;
        white-space: nowrap;
      }
      .url-desc{
        font-size: 12px;
        color: #b1b1b1;
        display: -webkit-box;
        -webkit-line-clamp:2;
        overflow: hidden;
        text-overflow: ellipsis;
        -webkit-box-orient: vertical;
      }
    }
  }
}
</style>



