<template>
  <div class="note-type-video">
    <div
      class="video"
      :style="getVideoStyle"
    >
      <i class="iconfont icon-bofang"></i>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    content: String
  },
  computed: {
    getVideoStyle() {
      return {'background-image': `url(${this.content && this.content.replace(/\\/g, '\\\\')}) !important`};
    }
  }
}
</script>
<style lang="scss" scoped>
.note-type-video{
  width: 100%;
  .video{
    width: 110px;
    height: 60px;
    background: #ececec;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    text-align: center;
    line-height: 60px;
    color: #ccc;
    .icon-bofang{
      font-size: 30px;
    }
  }
}
</style>



