<template>
  <div class="note-type-img">
    <viewer class="viewer" :images="getContentImgArr" :options="options">
      <img v-for="(item, index) in getContentImgArr" :src="item.thumb" :data-source="item.main" :key="index" width="50" @error="fixImg($event)">
    </viewer>
  </div>
</template>
<script>
import { mapGetters } from 'vuex'
export default {
  props: {
    content: Array
  },
  computed: {
    ...mapGetters(['note_list', 'user_account', 'http_head', 'user_imgPath']),
    getContentImgArr() {
      console.log('图片列表', this.content.imgArr);
      return this.content.imgArr;
    }
  },
  data() {
    return {
      failPic: require('../../assets/img/fileImg/picturefail.png'),
      options: { url: 'data-source' }
    }
  },
  methods: {
    fixImg(event) {
      console.log('错误图片', event);
      this.$nextTick(() => {
        event.target.src = this.failPic;
      })
    }
  }
}
</script>
<style lang="scss" scoped>
.note-type-img{
  width: 100%;
  display: flex;
  position: relative;
  .viewer{
    display: grid;
    grid-template-columns: repeat(3, 110px);
    img{
      width: 100px;
      height: 50px;
      min-width: 100px;
      min-height: 50px;
      margin-top: 10px;
      background: #ececec;
    }
  }
}
</style>



