<template>
  <div class="note-type-audio">
    <div class="audio">
      <i class="iconfont icon-voice_icon"></i>
    </div>
    <div class="title">
      {{$t('favorites.audio')}}
    </div>
  </div>
</template>
<script>
export default {
  props: {
    content: Object
  }
}
</script>
<style lang="scss" scoped>
.note-type-audio{
  width: 100%;
  display: flex;
  .audio{
    width: 60px;
    height: 60px;
    background: #ececec;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    text-align: center;
    line-height: 60px;
    .icon-voice_icon{
      font-size: 24px;
    }
  }
  .title{
    padding: 5px 10px;
  }
}
</style>



