import Vue from 'vue';

const note = {
  state: {
    account: {
      id: '7616956068',
      name: '王浩'
    },
    httpHead: 'https://vrv.linkdood.cn:10669/',
    noteList: [],
    searchList: [],
    userImgPath: '',
    userFilePath: '',
    userAudioPath: '',
    userVideoPath: ''
  },
  mutations: {
    UPDATE_IMG_PATH: (state, data) => {
      state.userImgPath = data;
    },
    UPDATE_FILE_PATH: (state, data) => {
      state.userFilePath = data;
    },
    UPDATE_AUDIO_PATH: (state, data) => {
      state.userAudioPath = data;
    },
    UPDATE_VIDEO_PATH: (state, data) => {
      state.userVideoPath = data;
    },
    SET_ACCOUNT: (state, data) => {
      state.account = data
    },
    SET_HTTPHEAD: (state, data) => {
      state.httpHead = data.preLoginAddress;
    },
    SET_NOTE: (state, data) => {
      switch (data.flag) {
        case 0: { // 添加收藏
          state.noteList.unshift(data.noteData);
          break;
        }
        case 1: { // 更新收藏数据
          state.noteList[0].content.mainUrl = data.mainUrl;
          break;
        }
        default: { // 第一次加载数据
          state.noteList = [];
          data.forEach(e => {
            // e.content = JSON.parse(e.content);
            state.noteList.push(e)
          })
        }
      }
    },
    SET_SEARCHLIST: (state, data) => {
      data.forEach(e => {
        e.content = JSON.parse(e.content)
      })
      state.searchList = data
    }
  }
};

export default note;



