<template>
  <div class="edit-box">
    <div
      class="editor-box"
      :class="placeholderTip"
      contenteditable="true"
      :data-placeholder="$t('favorites.createPlaceholder')"
      wrap="hard"
      ref="getMsg"
      @input="getScrollHeight"
    >
    </div>
    <div class="operat-area">
      <div class="left">
        <span>{{$t('favorites.createPlaceholder') + ':'}}</span>
        <span>{{10000-messagContent.length}}</span>
      </div>
      <div class="right">
        <button class="cancel" @click="$emit('close-edit')">{{$t('favorites.cancel')}}</button>
        <button
          :disabled="isDisabled"
          :class="{'is-disabled':isDisabled}"
          @click="addTxtNode"
        >{{$t('favorites.add')}}</button>
      </div>
    </div>
  </div>
</template>
<script>
// import { mapGetters } from 'vuex'
import { editBoxGetFocus } from './../common/util.js'
import noteMixin from './../common/noteMixin.js'
export default {
  mixins: [noteMixin],
  data() {
    return {
      messagContent: ''
    };
  },
  props: {},
  computed: {
    // ...mapGetters(['user_account']),
    placeholderTip() {
      return this.messagContent ? '' : 'placeholder-tip'
    },
    isDisabled() {
      return this.messagContent === ''
    }
  },
  mounted () {
    editBoxGetFocus(this.$refs.getMsg)
  },
  methods: {
    getScrollHeight () {
      this.messagContent = this.$refs.getMsg.innerText.substr(0, 10000);
    },
    addTxtNode() {
      let addNoteParam = {
        type: 2,
        content: {body: this.messagContent}
      };
      this.addNote(addNoteParam, true);
      this.$emit('close-edit');
    }
  }
}
</script>
<style lang="scss" scoped>
.edit-box{
  padding: 10px 5px;
  border-bottom: 1px solid #b5b5b5;
  .editor-box {
    width: 100%;
    min-height: 70px;
    max-height: 115px;
    padding: 3px;
    outline: none;
    font-size: 14px;
    word-wrap: break-word;
    overflow-x: hidden;
    overflow-y: auto;
    border-color: rgba(82, 168, 236, 0.8);
    -webkit-user-modify:read-write-plaintext-only;

  }
  .placeholder-tip{
    &::after  {
      content: attr(data-placeholder);
      color: #a9a9a9;
    }
  }
  .operat-area {
    display: flex;
    justify-content: space-between;
    padding-top: 10px;
    border-top: 1px dotted #b5b5b5;
    .left {
      width: 225px;
      height: 100%;
      .iconfont {
        font-size: 20px;
        padding-top: 5px;
        color: rgb(117, 117, 117);
        &:hover {
          color: rgb(36, 36, 36);
        }
      }
    }
    .right {
      width: auto;
      height: auto;
      position: relative;
      .showType-span{
        color: #757575;
        font-size: 13px;
        position: relative;
        padding-right: 30px;
        .drop-showType-icon{
          transform: rotate(-90deg);
          position: absolute;
            right: 11px;
          top: 0px;
          font-size: 16px;
        }
      }
      button {
        border: none;
        outline: none;
        border-radius: 5px;
        background: #565656;
        color: #fff;
        padding: 5px 20px;
        cursor: pointer;
      }
      .is-disabled{
        background: #7575756e;
        cursor: not-allowed;
      }
      .cancel{
        background: #e0e0e0;
        margin-right: 10px;
      }
    }
  }
}
</style>



