<template>
  <div class="main">
    <!-- 左侧收藏类型导航 -->
    <type-tab @class-title="getActiveTitle" @is-editBox="isEditBox = true"></type-tab>
    <!-- 页面正文 -->
    <div class="content">
      <!-- 顶部类型名称 -->
      <div class="title">{{classTitle}}</div>
      <!-- 创建文本收藏编辑区 -->
      <edit-box v-if="isEditBox" @close-edit="isEditBox=false"></edit-box>
      <!-- 收藏列表展示区 -->
      <contant-list :noteList="dataList" :isSearch="isSearch"></contant-list>
    </div>
  </div>
</template>
<script>
import { mapGetters } from 'vuex'
import TypeTab from './../list/TypeTab.vue';
import EditBox from './../edit/index.vue';
import ContantList from './../list/ContantList.vue';
import noteMixin from './../common/noteMixin.js';
export default {
  mixins: [noteMixin],
  data() {
    return {
      isEditBox: false,
      classTitle: this.$t('favorites.allFavorites'),
      noteType: 0,
      isSearch: false
    }
  },
  components: {
    TypeTab,
    EditBox,
    ContantList
  },
  computed: {
    ...mapGetters(['note_list', 'user_account', 'search_list']),
    dataList() {
      if (this.classTitle === this.$t('favorites.searchResult')) {
        return this.search_list
      } else {
        let classificationData = [];
        if (this.noteType === 0) return this.note_list;
        this.note_list.forEach((e, i) => {
          if (e.type === this.noteType) {
            console.log(1111);
            classificationData.push(e);
          }
        });
        return classificationData;
      }
    }
  },
  methods: {
    // 获取title显示文本
    getActiveTitle(e, t) {
      console.log(e, t);
      this.noteType = t;
      this.classTitle = e;
      this.isSearch = e === this.$t('favorites.searchResult')
    }
  }
};
</script>
<style lang="scss" scoped>
.main{
  width: 100%;
  height: 100%;
  display: flex;
  overflow: hidden;
  .content{
    flex: 1;
    background: #fff;
    overflow: hidden;
    width: 100%;
    .title{
      width: 100%;
      font-size: 18px;
      padding: 15px 25px;
      border-bottom: 1px solid #b5b5b5;
    }
  }
}
</style>



