<template>
  <div class="item-from">
    <div class="add-data">{{getTime(item.lastChgTime)}}</div>
    <div
      class="add-user"
    >{{$t('favorites.from')}}:{{userName}}{{getUserName(item.sendUserId)}}</div>
  </div>
</template>
<script>
import { getLocalTime } from './../../components/common/util.js';
export default {
  data() {
    return {
      userName: ''
    }
  },
  props: {
    item: Object
  },
  methods: {
    // 时间戳格式化标准时间
    getTime(timestamp) {
      return getLocalTime(timestamp)
    },
    getUserName(id) {
      this.$Service.search.getUserInfo(
        { userId: id },
        resp => {
          if (!resp || resp.code !== 0) return
          this.userName = resp.users && resp.users.name
        }
      )
    }
  }
}
</script>
<style lang="scss">
.item-from{
  width: 200px;
  color: #b1b1b1;
  font-size: 12px;
  text-align: end;
  div{
    padding: 2px 0;
  }
}
</style>



