<template>
  <div id="app">
    <router-view/>
  </div>
</template>

<script>
import minxin from './components/common/noteMixin.js';
// mock数据
const noteData = require('./../static/data.json');
const newData = require('./../static/newData.json');
export default {
  mixins: [minxin],
  name: 'App',
  created () {
    console.log('get imsdkservice: ', this)
    console.log('i get env: ', process.env.NODE_ENV);
    let agent = navigator.userAgent.toLowerCase();
    if (process.env.NODE_ENV === 'development') {
      let loginParam = {
        user: '008618791864382', // 用户名 手机号要加 0086
        pwd: 'hx970501', // 密码
        userType: 1, // 用户类型
        server: 'vrv',
        nation: '0086'
      };
      this.$Service.auth.login(loginParam, resp => {
        console.log('i get login result: ', resp);
        if (resp.code === 0 || resp.code === 113) {
          this.$router.push({ path: '/Main' });
          this.getNoteList(); // 获取收藏列表
          this.getAccountInfo(); // 获取账号信息
          this.getImgPath() // 获取图片路径
        }
      })
    } else {
      this.$router.push({ path: '/Main' });
      this.getNoteList(); // 获取收藏列表
      this.getAccountInfo(); // 获取账号信息
      this.getImgPath() // 获取图片路径
    }
  },
  mouted() {
    document.oncontextmenu = () => { return false; }
  },
  methods: {
    getImgPath() {
      this.$Service.config.userImgPath(null, resp => {
        console.log(`user_imgPath:::`, resp);
        this.$store.commit('UPDATE_IMG_PATH', resp.path);
      });
      this.$Service.config.userFilePath(null, resp => {
        console.log(`user_filePath:::`, resp);
        this.$store.commit('UPDATE_FILE_PATH', resp.path);
      });
      this.$Service.config.userAudioPath(null, resp => {
        console.log(`user_audioPath:::`, resp);
        this.$store.commit('UPDATE_AUDIO_PATH', resp.path);
      });
      this.$Service.config.userVideoPath(null, resp => {
        console.log(`user_videoPath:::`, resp);
        this.$store.commit('UPDATE_VIDEO_PATH', resp.path);
      });
    },
    // 获取账户信息
    getAccountInfo() {
      this.$Service.user.getAccountInfo(null, resp => {
        // this.$Service.log(`获取账户信息回调${JSON.stringify(resp)}`);
        this.$store.commit('SET_ACCOUNT', resp);
        this.getSecUrl(resp.server)
      })
    },
    // 获取预登陆服务器地址
    getSecUrl(val) {
      this.$Service.auth.getSecUrl(
        {server: val},
        resp => {
          // this.$Service.log(`预登录地址${JSON.stringify(resp)}`);
          this.$store.commit('SET_HTTPHEAD', {preLoginAddress: resp.value + '/'});
        }
      );
    }
  }
}
</script>
<style>
#app {
  font-family: '\5FAE\8F6F\96C5\9ED1', 'Microsoft YaHei', Arial, Helvetica, sans-serif, '\5B8B\4F53';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 14px;
  color: #000;
  background: #eff4f8;
  width: 100%;
  height: 100%;
}
::-webkit-scrollbar{
  width: 6px;
  background: rgba(255, 0, 0, 0);
  z-index: 888;
}
::-webkit-scrollbar-thumb{
  width: 6px;
  border-radius: 6px;
  background: #c7c7c7;
}
input, button{
  font-family: '\5FAE\8F6F\96C5\9ED1', 'Microsoft YaHei', Arial, Helvetica, sans-serif, '\5B8B\4F53'
}
</style>



