<template>
  <div class="txt-preview">
    <div class="" v-html="content.body.replace(/\n/g, '<br/>')"></div>
  </div>
</template>

<script>
export default {
  props: {
    content: Object
  }
}
</script>

<style lang="scss" scoped>
.txt-preview{
  width: 100%;
  height: 100%;
  background: #fff;
  // border-radius: 0 0 4px 4px;
  overflow: hidden;
  overflow-y: auto;
}
</style>



