import { mapGetters } from 'vuex';
import i18n from '../../assets/common/i18n';
const sousuoData = require('./../../../static/sousuo.json');
const mixin = {
  data() {
    return {
      // addNoteParam: {
      //   isNet: false, // 是否使用网络接口
      //   lastChgTime: new Date().getTime().toString(), // 最后修改时间,毫秒  创建时间
      //   localPath: '', // 本地文件地址*
      //   type: -1, // 1. 文本，2，图片，3，音频，4，视频，5，URL
      //   netType: 1, // 1. 本地， 0. 网络
      //   content: {} // 内容 最长4096个字符收藏消息 文本、URL 直接存储内容   非文本类型，存储附件地址，网络地址和本地地址均可。。
      // },
      // localNote: {
      //   title: '', // <标题 最长128字符
      //   isReminder: 0, // <是否是提醒
      //   rmdTime: 0, // <提醒时间
      //   isArchive: 0 // <是否归档
      // },
      // netNote: {
      //   msgProperties: '', // <消息属性
      //   fileName: '', // <文件名
      //   exts: '', // <扩展字段
      //   loginInfo: ''
      // },
      addNoteParam: {
        content: '',
        exts: '',
        fileName: '',
        id: '',
        isArchive: 0,
        isNet: true,
        isReminder: 0,
        key: '',
        lastChgTime: new Date().getTime().toString(),
        loginInfo: '',
        msgProperties: '',
        netType: 0,
        rmdTime: '0',
        sendUserId: '',
        targetId: '',
        title: '',
        type: Number
      }
    }
  },
  computed: {
    ...mapGetters(['user_account', 'note_list'])
  },
  methods: {
    // 获取收藏列表
    getNoteList() {
      console.log("i getNoteList");
      this.$Service.enterprise.getNote(
        {beginID: 0, offset: 1000, offsetFlag: 0},
        this._getNoteListHandle
      )
    },
    _getNoteListHandle(resp) {
      console.log('get note Cb:::', resp.infos);
      if (resp.code === 0) {
        let arr = resp.infos;
        arr.forEach(element => {
          this.$set(element, 'content', JSON.parse(element.content))
        });
        this.$store.commit('SET_NOTE', arr)
      }
    },
    // 添加收藏
    addNote(msgInfo, isAddNote) {
      // 公用参数
      this.addNoteParam.id = ''; // 记事本唯一标识,收藏ID this.note_list.length + 1
      this.addNoteParam.sendUserId = this.user_account.id; // 发送的人 消息来源Id
      this.addNoteParam.targetId = this.user_account.id; // 来源 人或者群  群Id
      Object.assign(this.addNoteParam, msgInfo);
      // 构造界面显示数据
      if (!isAddNote) {
        this.$store.commit('SET_NOTE', {flag: 0, noteData: this.addNoteParam});
        return
      };
      // let tempAddNoteParam = Object.assign({}, this.addNoteParam);
      // tempAddNoteParam.content = JSON.stringify(tempAddNoteParam.content);
      this.addNoteParam.content = JSON.stringify(this.addNoteParam.content);
      console.log(this.addNoteParam, '添加收藏入参Json');
      this.$Service.enterprise.addNote(
        this.addNoteParam,
        resp => {
          console.log(resp, 'add note cb')
          if (resp.code === 0) {
            this.$toast.tip({message: i18n.t('favorites.createSuccess')})
            this.getNoteList();
          }
        }
      )
    }
  }
}
export default mixin;



