<template>
  <div class="audio-preview">
    <div class="audio-simulation">
      <div class="controls">
        <i @click="play" class="iconfont" :class="getPlayState"></i>
      </div>
      <div class="play-details">
        <span class="current-time">{{currentTime}}</span>
        <div class="progress-bar">
          <div class="progress" :style="{width: progressWidth + '%'}"></div>
        </div>
        <span class="all-time">{{allTime}}</span>
      </div>
    </div>
    <audio
      :src="getAudioPlayPath"
      controls="controls"
      preload="auto"
      ref="audio"
      @canplay="readyPlay"
      @timeupdate="getCurrentTime"
    ></audio>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';
import AMR from './amrnb.js';

export default {
  data() {
    return {
      isPlay: true,
      progressWidth: 0,
      audio: null,
      currentTime: '00:00',
      allTime: '00:00',
      canPlay: false,
      decryptFiles: '', // 解密文件
      audioPlayPath: this.audioPath // 音频文件播放路径
    }
  },
  computed: {
    ...mapGetters(['http_head', 'user_audioPath']),
    getPlayState() {
      if (this.canPlay) {
        if (this.isPlay) {
          return 'icon-bofang1';
        } else {
          return 'icon-zanting';
        }
      }
      return ['icon-jiazaizhong', 'loading']
    },
    getAudioPlayPath() {
      if (this.audioPlayPath) {
        return 'file://' + this.audioPlayPath;
      } else {
        return '';
      }
    }
  },
  props: {
    content: Object,
    audioPath: String
  },
  mounted() {
    this.audio = this.$refs.audio;
    console.log('音频文件播放路径->', this.audioPlayPath);
  },
  watch: {
    audioPath() {
      this.audioPlayPath = this.audioPath;
      console.log('pcAudioPath-hasChange', this.audioPlayPath);
    }
  },
  methods: {
    readyPlay() {
      this.canPlay = true;
      let time = Math.round(this.audio.duration);
      this.allTime = this.getTimeFormat(parseInt(time / 60)) + ':' + this.getTimeFormat(time % 60);
    },
    getCurrentTime() {
      let cTime = Math.round(this.audio.currentTime);
      this.currentTime = this.getTimeFormat(parseInt(cTime / 60)) + ':' + this.getTimeFormat(cTime % 60);
      this.progressWidth = this.audio.currentTime / this.audio.duration * 100
      if (this.audio.paused) {
        this.isPlay = true;
        this.currentTime = '00:00';
        this.progressWidth = 0;
      }
    },
    getTimeFormat(time) {
      return time > 9 ? time : ('0' + time);
    },
    play() {
      if (this.canPlay) {
        console.log(this.allTime);
        if (this.audio.paused) {
          this.audio.play();
          this.isPlay = false
        } else {
          this.audio.pause();
          this.isPlay = true
        }
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.audio-preview{
  background: #fff;
  flex: 1;
  padding: 20px!important;
  audio{
    background: #000;
    display: none
  }
  .audio-simulation{
    background: #fff;
    border: 1px solid #a9a9a9;
    width: 410px;
    height: 60px;
    display: flex;
    .controls{
      width: 60px;
      height: 100%;
      text-align: center;
      line-height: 59px;
      i{
        font-size: 40px;
        color: #565656;
        text-align: center;
        cursor: pointer;
      }
      .loading {
        display: inline-block;
        animation: loading 2000ms linear infinite;
      }
    }
    .play-details{
      width: 100%;
      flex: 1;
      display: flex;
      align-items: center;
      color: #888;
      span{
        width: 40px;
      }
      .progress-bar{
        width: 240px;
        height: 4px;
        margin: 0 10px;
        background: #e0e0e0;
        .progress {
          height: 100%;
          background: #757575;
        }
      }
    }
  }
}
@keyframes loading {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
</style>



